import * as S from "./style";
import { css } from "@emotion/react";
interface CategoryProps {
  menu: string[];
}

const Category = ({ menu }: CategoryProps) => {
  return (
    <S.Category>
      <S.MenuWrap>
        {menu.map((i) => {
          return (
            <S.Menu key={i}>
              <S.Dot />
              {i}
            </S.Menu>
          );
        })}
      </S.MenuWrap>
    </S.Category>
  );
};

export default Category;
